import * as THREE from "three";

import {SpriteText2D, textAlign} from "three-text2d";

const fontHeightCache = {};
function getFontHeight(fontStyle) {
  let result = fontHeightCache[fontStyle];
  if (!result) {
    const body = document.getElementsByTagName('body')[0];
    const dummy = document.createElement('div');
    const dummyText = document.createTextNode('MÉq');
    dummy.appendChild(dummyText);
    dummy.setAttribute('style', "font:" + fontStyle + ";position:absolute;top:0;left:0");
    body.appendChild(dummy);
    result = dummy.offsetHeight;
    fontHeightCache[fontStyle] = result;
    body.removeChild(dummy);
  }
  return result;
}

function MultilineSpriteText2D(text, options) {
	this.width = options.width;
	this.lineHeight = options.lineHeight;
	SpriteText2D.apply(this, arguments);
	this.canvas.drawText = function (text, ctxOptions) {
    this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);
    this.ctx.font = ctxOptions.font;
		this.ctx.fillStyle = ctxOptions.fillStyle;
		this.ctx.textAlign = 'left';
		this.ctx.textBaseline = 'top';
		this.ctx.shadowColor = ctxOptions.shadowColor;
		this.ctx.shadowBlur = ctxOptions.shadowBlur;
		this.ctx.shadowOffsetX = ctxOptions.shadowOffsetX;
		this.ctx.shadowOffsetY = ctxOptions.shadowOffsetY;
		const lineHeight = getFontHeight(ctxOptions.font);
		const words = mikan.split(text);
		let width = 0;
		const lines = [];
		let lineIndex = 0;
		lines[lineIndex] = "";
		words.forEach(word => {
			const wordWidth = this.ctx.measureText(word).width;
			if (this.width - width >= wordWidth) {
				lines[lineIndex] += word;
				width += wordWidth;
			} else {
				lineIndex++;
				lines[lineIndex] = "";
				width = 0;
			}
		});
		this.textWidth = 0;
		lines.forEach(line => {
			this.textWidth = Math.max(this.textWidth, Math.ceil(this.ctx.measureText(line).width));
		});
		this.textHeight = lineHeight * this.lineHeight * lines.length;
		this.canvas.width = Math.max(1, THREE.Math.nextPowerOfTwo(this.textWidth));
		this.canvas.height = Math.max(1, THREE.Math.nextPowerOfTwo(this.textHeight));
		let y = 0;
		lines.forEach(line => {
	    this.ctx.fillText(line, 0, y);
			y += lineHeight * this.lineHeight;
		});
    return this.canvas;
	};
	return this;
};

MultilineSpriteText2D.prototype = Object.create(SpriteText2D.prototype);

Object.defineProperty(MultilineSpriteText2D.prototype, "width", {
  value: null,
  enumerable: true,
  configurable: true,
	writable: true
});

export function createRectangle(options) {
	const mesh = new THREE.Mesh(
		new THREE.PlaneBufferGeometry(options.width, options.height),
		new THREE.MeshBasicMaterial({color: options.fillColor})
	);
	mesh.opacity = options.opacity;
	mesh.selfOpacity = options.selfOpacity;
	return mesh;
};

export function createCircle(options) {
	const mesh = new THREE.Mesh(
		new THREE.CircleBufferGeometry(options.radius, options.segments !== undefined ? options.segments : 32),
		new THREE.MeshBasicMaterial({color: options.fillColor})
	);
	mesh.opacity = options.opacity;
	mesh.selfOpacity = options.selfOpacity;
	return mesh;
};

export function createMark(options) {
	const group = new THREE.Group();
	group.material = new THREE.MeshBasicMaterial({color: options.strokeColor});
	group.vertical = new THREE.Mesh(
		new THREE.PlaneBufferGeometry(options.strokeWidth, options.height),
		group.material
	);
	group.horizontal = new THREE.Mesh(
		new THREE.PlaneBufferGeometry(options.width, options.strokeWidth),
		group.material
	);
	group.add(group.vertical);
	group.add(group.horizontal);
	group.opacity = options.opacity;
	group.selfOpacity = options.selfOpacity;
	return group;
};

export function createLabelArea(text, options) {
	options = Object.defaults(options, {
		align: textAlign.center,
		fillStyle: 'hsla(0, 0%, 0%, 0.6)',
		font: "32px 'HiraKakuProN-W3'"
	});
	return new MultilineSpriteText2D(text, options);
}
