import Scene from "./scene";

import * as THREE from "three";
import {SpriteText2D, textAlign} from "three-text2d";

import Loader from "resource-loader";

import Easing from "./easing";

import {renderScreen} from "./main";

export const assets = {
	THREE_Model_JSON: {
		fighter: 'data/models/fighter-1.min.json',
		bullet: 'data/models/bullet-lq.min.json',
		enem1: 'data/models/enem-1.min.json',
		airballoon: 'data/models/airballoon.min.json',
	},
	THREE_Texture: {
		//fighter: 'data/models/fighter-1.png',
		plane: 'data/images/3.png',
		explode: 'data/images/explosion.png'
	},
	GLSL: {
		expvertex: 'data/glsl/expvertexshader.min.glsl',
		expfrag: 'data/glsl/expfragshader.min.glsl',
		glowvertex: 'data/glsl/glowvertexshader.min.glsl',
		glowfrag: 'data/glsl/glowfragshader.min.glsl',
		noisyglowvertex: 'data/glsl/noisyglowvertexshader.min.glsl'
	}
};



export class LoadingScene extends Scene {
	constructor(list, nextScene, ...nextSceneArguments) {
		super();
		this.label = new SpriteText2D("Loading...", {
			align: textAlign.center,
			font: '20px Arial',
			fillStyle: '#444a',
		});
		this.UIScene.add(this.label);
		const loader = setupLoader(list);
		loader.onProgress.add(() => {
  		this.label.text = "Loading... " + Math.round(loader.progress) + "%";
			renderScreen();
		});
		loader.load(() => {
  		console.log('Load complete');
			this.addEasing(
				new Easing(this.label)
					.add({opacity: 0}, 200, Easing.LINEAR)
					.trigger(nextScene.createAndEnter(...nextSceneArguments))
			);
		});
	}
}

export function setupLoader(list) {
	const loader = new Loader();
	let currentParentResource = null;

	const loadTexture = {load(fullPath) {
		const texture = new THREE.Texture();
		loader.add(fullPath, fullPath, {parentResource: currentParentResource}, resource => {
			texture.image = resource.data;
			texture.needsUpdate = true;
		});
		return texture;
	}};
	THREE.Loader.Handlers.add(/.*/, loadTexture);
	Object.forEach(list, (category, categoryname) => category.forEach(name => {
		loader.add(categoryname + "." + name, assets[categoryname][name], resource => {
			//console.log(categoryname, resource.data);
			if (categoryname === "THREE_Model_JSON") {
				currentParentResource = resource;
				const data = new THREE.JSONLoader().parse(
					resource.data,
					THREE.LoaderUtils.extractUrlBase(assets[categoryname][name])
				);
				assets[categoryname][name] = new THREE.Mesh(data.geometry, data.materials);
			} else if (categoryname === "THREE_Texture") {
				assets[categoryname][name] = new THREE.Texture(resource.data);
				assets[categoryname][name].needsUpdate = true;
			} else {
				assets[categoryname][name] = resource.data;
			}
		});
	}));
	loader.onComplete.add(() => THREE.Loader.Handlers.handlers.splice(THREE.Loader.Handlers.handlers.indexOf(loadTexture), 1));
	return loader;
}
