import * as THREE from "three";
import {SpriteText2D, textAlign} from "three-text2d";

import ShaderPass from "../libs/three-effect/ShaderPass";

import * as THREE_Utils from "./threeutil";

import Scene, {hitboxes} from "./scene";
import {assets} from "./loading";
import skills from "./skills";
import {vw, vh} from "./global";
import {createRectangle, createCircle, createMark, createLabelArea} from "./geometries";
import Easing from "./easing";
import {threeComposer} from "./main";

function createLabel(text, options) {
	return new SpriteText2D(text || " ", Object.defaults(options, {
		align: textAlign.center,
		fillStyle: 'hsla(0, 0%, 0%, 0.6)',
		font: "32px 'HiraKakuProN-W3'"
	}));
}

const BASE_Z = 100;

export default class TitleScene extends Scene {
	constructor() {
		super();
		const start = () => {
			const fade = new ShaderPass(FadeShader);
			this.threePasses.push(fade);
			const zoomblur = new ShaderPass({
				uniforms: {
					tDiffuse: {value: null},
					strength: {value: 0},
				},

				vertexShader: "varying vec2 vUv;void main() {vUv=uv;gl_Position=projectionMatrix*modelViewMatrix*vec4(position,1.0);}",

				fragmentShader: "uniform float strength;uniform sampler2D tDiffuse;varying vec2 vUv;const float nFrag=1.0/30.0;float rnd(vec3 scale,float seed){return fract(sin(dot(gl_FragCoord.stp+seed,scale))*43758.5453+seed);}void main(void){vec4 destColor=vec4(0.0);float random=rnd(vec3(12.9898,78.233,151.7182),0.0);float totalWeight=0.0;for(float i=0.0;i<=30.0;i++){float percent=(i+random)*nFrag;float weight=percent-percent*percent;destColor+=texture2D(tDiffuse,vUv-(vUv-vec2(0.5,0.5))*percent*strength*nFrag)*weight;totalWeight+=weight;}gl_FragColor=destColor/totalWeight;}"
			});
			this.app.composer.addPass(zoomblur);
			this.startframe = 0;

			this.on('enterframe', () => {
				fade.uniforms.color.value.w = this.startframe * 0.025;
				zoomblur.uniforms.strength.value = this.startframe * 0.4;
				if (this.startframe === 40) this.exit(nowarg);
				else this.startframe++;
			});
		};
		const nowarg = {};
		nowarg.skills = JSON.parse(localStorage.getItem('skills-pre')) || [
			{klass: Railgun, level: 0},
			{klass: Empty, level: 0},
			{klass: Empty, level: 0},
			//{klass: BladeMinion, level: 0},
			//{klass: Reinforce, level: 2},
			{klass: SelfRepair, level: 0}
		];
		nowarg.skills.forEach(skill => {
			if (skill.name) skill.klass = skills.byName[skill.name];
		});
		const menu = {
			title: {
				x: 0, y: 0, sub: [
					{type: 'label', value: 'Forever Flight', y: 15, size: 3.6},
					{type: 'label', value: 'Click to start', y: -15, size: 1.8},
					{type: 'model', name: 'player', value: assets.THREE_Model_JSON.fighter, x: 0, y: -5, z: -50},
					{
						type: 'model', value: new THREE.Mesh(new THREE.CircleGeometry(10000, 100), new THREE.MeshBasicMaterial({
							map: assets.THREE_Texture.plane
						})), x: 0, y: 1000, z: 0, init: model => THREE_Utils.rotateX(model, -Math.PI / 2)
					}
				]
			},
			main: {
				x: 50, y: -50, sub: [
					{type: 'label', value: 'Main Menu', y: 14, size: 3.6},
					{type: 'label', value: 'Campaign', y: 8, size: 1.8, link: 'difficulty'},
					{type: 'label', value: 'Stage Select', y: 4, size: 1.8, link: 'stageselect'},
					{type: 'label', value: 'Ship Select', size: 1.8, link: 'shipselect'},
					{type: 'label', value: 'Free Play', y: -4, size: 1.8, link: 'difficulty', callback: () => {nowarg.stage = 'arcade'}},
					{type: 'label', value: 'How to play', y: -8, size: 1.8, link: 'help'},
					{type: 'label', value: 'Settings', y: -12, size: 1.8, link: 'setting'},
					{type: 'label', value: 'Credit', x: 8, y: -18, size: 1.5, link: 'credit'},
					{type: 'label', value: 'Back', y: -16, size: 1.8, link: 'title'}
				]
			},
			help: {
				x: -60, y: 55, sub: [
					{type: 'label', value: 'How to play', y: 14, size: 3.6},
					{type: 'label', value: 'Your airplane moves automatically', y: -1, size: 0.8},
					{type: 'label', value: 'to direction of your mouse pointer', y: -2, size: 0.8},
					{type: 'label', value: '>', x: 12, size: 2.5, link: 'help2'},
					{type: 'label', value: '(1/5)', y: -3, size: 0.8},
					{type: 'label', value: 'Main Menu', y: -16, size: 1.8, link: 'main'}
				]
			},
			help2: {
				x: 0, y: 55, sub: [
					{type: 'label', value: 'Speed-up by pressing your left mouse button', y: -1, size: 0.8},
					{type: 'label', value: '<', x: -12, size: 2.5, link: 'help'},
					{type: 'label', value: '>', x: 12, size: 2.5, link: 'help3'},
					{type: 'label', value: '(2/5)', y: -3, size: 0.8},
					{type: 'label', value: 'Main Menu', y: -16, size: 1.8, link: 'main'}
				]
			},
			help3: {
				x: 60, y: 55, sub: [
					{type: 'label', value: 'Press W/S key to up/down vertically', y: -1, size: 0.8},
					{type: 'label', value: '(any key in this help is default key bind)', y: -2, size: 0.8},
					{type: 'label', value: '<', x: -12, size: 2.5, link: 'help2'},
					{type: 'label', value: '>', x: 12, size: 2.5, link: 'help4'},
					{type: 'label', value: '(3/5)', y: -3, size: 0.8},
					{type: 'label', value: 'Main Menu', y: -16, size: 1.8, link: 'main'}
				]
			},
			help4: {
				x: 120, y: 55, sub: [
					{type: 'label', value: 'Press space key to attack', y: -1, size: 0.8},
					{type: 'label', value: '<', x: -12, size: 2.5, link: 'help3'},
					{type: 'label', value: '>', x: 12, size: 2.5, link: 'help5'},
					{type: 'label', value: '(4/5)', y: -3, size: 0.8},
					{type: 'label', value: 'Main Menu', y: -16, size: 1.8, link: 'main'}
				]
			},
			help5: {
				x: 180, y: 55, sub: [
					{type: 'label', value: 'A/D key and Shift key to use up to 4 kind of skill', y: -1, size: 0.8},
					{type: 'label', value: '<', x: -12, size: 2.5, link: 'help4'},
					{type: 'label', value: '(5/5)', y: -3, size: 0.8},
					{type: 'label', value: 'Main Menu', y: -16, size: 1.8, link: 'main'}
				]
			},
			stageselect: {
				x: -72, y: -25, sub: [
					{type: 'label', value: 'Stage Select', y: 14, size: 2.5},
					{type: 'label', value: 'Main Menu', y: -16, size: 1.8, link: 'main'},
					{
						type: 'model', name: 'airballoon', value: assets.THREE_Model_JSON.airballoon, x: 0, y: 0, z: 0,
						init: (model) => THREE_Utils.rotate(model, new THREE.Vector3(1, -1, -1).normalize(), 1)
					},
				]
			},
			shipselect: {
				x: 0, y: 0, z: -20, sub: [
					{type: 'label', value: 'Ship Select', y: 14, size: 3.6},
					{type: 'label', value: '(You cannot select ship in this version)', y: -4, size: 0.8},
					{type: 'label', value: '<', x: -12, size: 2.5},
					{type: 'label', value: '>', x: 12, size: 2.5},
					{type: 'label', value: 'Modify Ship', y: -8, size: 1.8, link: 'shipmodify'},
					{type: 'label', value: 'Main Menu', y: -16, size: 1.8, link: 'main'},
				]
			},
			shipmodify: {
				x: 0, y: 0, z: -40, sub: [
					{type: 'label', value: 'Ship Modify', y: 14, size: 3.6},
					{type: 'point', parent: 'player', index: 0, place: "front", x: 2, y: 4, z: 24},
					{type: 'point', parent: 'player', index: 1, place: "front", x: -2, y: 4, z: 24},
					{type: 'point', parent: 'player', index: 2, place: "top", x: 0, y: 7, z: -5},
					{type: 'point', parent: 'player', index: 3, place: "core", x: 0, y: 3, z: 0},
					{type: 'label', value: 'Back', y: -12, size: 1.8, link: 'shipselect'},
					{type: 'label', value: 'Main Menu', y: -16, size: 1.8, link: 'main'},
				]
			},
			difficulty: {
				x: 125, y: -100, sub: [
					{type: 'label', value: 'Difficulty', y: 16, size: 3.6},
					{type: 'label', value: 'Easy', y: 4, size: 1.8, callback: () => {nowarg.difficulty = 0.8;start()}},
					{type: 'label', value: 'Normal', size: 1.8, callback: start},
					{type: 'label', value: 'Hard', y: -4, size: 1.8, callback: () => {nowarg.difficulty = 1.25;start()}},
					{type: 'label', value: 'Back', y: -8, size: 1.8, link: 'main'},
					{
						type: 'model', name: 'enem1', value: assets.THREE_Model_JSON.enem1.clone(), x: 8, y: -4, z: 0,
						init: model => THREE_Utils.rotate(model, new THREE.Vector3(1, -1, -1).normalize(), 1)
					},
					{
						type: 'model', name: 'enem1', value: assets.THREE_Model_JSON.enem1.clone(), x: -8, y: 4, z: 0,
						init: model => THREE_Utils.rotate(model, new THREE.Vector3(1, -1, -1).normalize(), 1)
					},
					{
						type: 'model', name: 'enem1', value: assets.THREE_Model_JSON.enem1.clone(), x: -10, y: -12, z: 40,
						init: model => THREE_Utils.rotate(model, new THREE.Vector3(1, -1, -1).normalize(), 1)
					}
				]
			},
			setting: {
				x: 25, y: -144, sub: [
					{type: 'label', value: 'Settings', y: 14, size: 3.6},
					{type: 'label', value: 'KeyBinding', y: 5, size: 1.8},
					{type: 'label', value: 'Sound Volume', y: -5, size: 1.8},
					{type: 'label', value: 'Back', y: -16, size: 1.8, link: 'main'}
				]
			},
			credit: {
				x: 500, y: -500, sub: [
					{type: 'label', value: 'Credit', y: 14, size: 3.6},
					{type: 'label', value: 'Programing: axion014', y: 8, size: 1.8},
					{type: 'label', value: 'Back', y: -16, size: 1.8, link: 'main'}
				]
			}
		}

		const directionalLight = new THREE.DirectionalLight(0xffffff, 1);
		directionalLight.position.set(0, 0, 30);
		this.threeScene.add(directionalLight);
		this.threeScene.add(new THREE.AmbientLight(0x606060));
		threeComposer.renderer.setClearColor(0x66aaee, 1.0);
		this.threeScene.fog = new THREE.FogExp2(0x66aaee, 0.00025);
		this.camera.position.z = BASE_Z;
		this.time = 0;

		const moveTo = (x, y, z) => {
			const dist = Math.hypot(this.camera.position.x - x, this.camera.position.y - y);
			this.addEasing(new Easing(this.camera.position).add(
				{x: x, y: y, z: BASE_Z + z},
				Math.max(dist * 3, 900),
				Easing.inOut(dist / 125 + 2.5)
			));
		};
		const moveToMain = () => {
			moveTo(50, -50, 0);
			this.removeEventListener('click', moveToMain);
		};
		this.labels = [];
		this.points = [];
		const equipmentEdit = createRectangle({
			width: vw * 0.75,
			height: vh * 0.75,
			fillColor: "#666",
			opacity: 0,
			selfOpacity: 0.5
		});
		this.UIScene.add(equipmentEdit);

		this.addEventListener('click', (e) => {
			this.labels.forEach(label => {
				if (label.interactive === false) return;
				const pos = label.position.clone().project(this.camera);
				const rect = e.currentTarget.getBoundingClientRect();
				const x = e.clientX - rect.left;
				const y = e.clientY - rect.top;
				if (
					Math.abs(x - (pos.x + 1) * vw / 2) < label.width / 2 &&
						Math.abs(y - (1 - pos.y) * vh / 2) < label.height / 2 &&
						Math.abs(this.camera.position.z - label.position.z - 50) < 1) {
					label.dispatchEvent(e);
				}
			});
		});

		Object.forEach(menu, value => {
			value.z = value.z || 0;
			value.sub.forEach(selects => {
				if (selects.type === 'label') {
					selects = Object.defaults(selects, {x: 0, y: 0});
					const pixratio = 32;
					const label = createLabel(selects.value, {
						align: new THREE.Vector2(0, 0.5),
						font: (selects.size * pixratio) + "px 'HiraKakuProN-W3'"
					});
					label.scale.set(1 / pixratio, 1 / pixratio, 1);
					this.threeScene.add(label);
					label.position.set(value.x + selects.x, value.y + selects.y, value.z + 50);
					label.material.opacity = (1 - Math.min(Math.max(Math.abs(value.z) - 10, 0) * 0.1, 1)) * 0.5;
					if (selects.link) {
						label.addEventListener('click', () => {
							moveTo(menu[selects.link].x, menu[selects.link].y, menu[selects.link].z || 0);
							if (selects.link === 'title') this.addEventListener('click', moveToMain);

							if (selects.link === 'shipmodify') this.points.forEach(point => point.visible = true);
							else this.points.forEach(point => point.visible = false);

							this.position = selects.link;
						});
					}
					if (selects.callback) label.addEventListener('click', selects.callback);
					this.labels.push(label);
				} else if (selects.type === 'model') {
					const add = (parent, models) => {
						models.forEach(model => {
							parent.add(model.value);
							model.value.position.set(value.x + model.x, value.y + model.y, value.z + model.z);
							if(model.init) model.init(model.value);
							if(model.name) this[model.name] = model.value;
							model.childrens && add(model.value, model.childrens);
						});
					}
					add(this.threeScene, [selects]);
				} else if (selects.type === 'point') {
					const scene = this;
					class EquipSlot extends THREE.Group {
						constructor(options) {
							super();
							this.hitbox = hitboxes.CIRCLE;
							this.radius = 16;
							this.data = options.data;
							this.visible = false;
							this.add(createMark({strokeColor: "#4a4", strokeWidth: 1, width: 48, height: 48}));
							this.add(createCircle({fillColor: "#4a4", radius: 16, opacity: 0.5}));
							this.addEventListener('click', () => {
								scene.interactive = false;
								scene.points.forEach(point => point.interactive = false);
								equipmentEdit.target = this.data;
								scene.UIScene.add(equipmentEdit);
								scene.addEasing(new Easing(equipmentEdit).add({opacity: 1}, 250, Easing.LINEAR));
								equipmentEdit.updateCurrent(nowarg.skills[this.data.index].klass, nowarg.skills[this.data.index].level);
							});
						}
					}
					const slot = new EquipSlot({data: selects});
					slot.position.set(selects.x, selects.y, 0);
					this.UIScene.add(slot);
					this.points.push(slot);
				}
			});
		});
		this.addEventListener('click', moveToMain);

		equipmentEdit.skill = {};
		equipmentEdit.name = createLabel();
		equipmentEdit.add(equipmentEdit.name);
		equipmentEdit.name.position.y = vh * 0.19;
		equipmentEdit.description = createLabelArea(" ", {
			width: vw * 0.42,
			font: "18px 'HiraKakuProN-W3'"
		});
		equipmentEdit.add(equipmentEdit.description);
		equipmentEdit.description.y = -vh * 0.06;

		const left = createLabel("<", {font: "48px 'HiraKakuProN-W3'"});
		const right = createLabel(">", {font: "48px 'HiraKakuProN-W3'"});
		const up = createLabel("<", {font: "48px 'HiraKakuProN-W3'"});
		const down = createLabel(">", {font: "48px 'HiraKakuProN-W3'"});

		left.position.x = -vw * 0.28;
		right.position.x = vw * 0.28;
		up.position.y = vh * 0.125;
		down.position.y = -vh * 0.25;

		up.rotateZ(Math.PI / 2);
		down.rotateZ(Math.PI / 2);

		left.addEventListener('click', () => {
			let index = skills[equipmentEdit.target.place].indexOf(equipmentEdit.skill.klass);
			do {
				if (index === 0) {
					let klass = skills[equipmentEdit.target.place][index = skills[equipmentEdit.target.place].length - 1];
				} else {
					let klass = skills[equipmentEdit.target.place][--index];
				}

			} while (klass.unlockedLevel < 0);
			equipmentEdit.updateCurrent(klass, Math.min(equipmentEdit.skill.level, klass.unlockedLevel));
		});

		right.addEventListener('click', () => {
			let index = skills[equipmentEdit.target.place].indexOf(equipmentEdit.skill.klass);
			do {
				if (index === skills[equipmentEdit.target.place].length - 1) {
					let klass = skills[equipmentEdit.target.place][index = 0];
				} else {
					let klass = skills[equipmentEdit.target.place][++index];
				}

			} while (klass.unlockedLevel < 0);
			equipmentEdit.updateCurrent(klass, Math.min(equipmentEdit.skill.level, klass.unlockedLevel));
		});

		up.addEventListener('click', () => {
			if (equipmentEdit.skill.level < equipmentEdit.skill.klass.unlockedLevel)
				equipmentEdit.updateCurrent(equipmentEdit.skill.klass, equipmentEdit.skill.level + 1);
		});

		down.addEventListener('click', () => {
			if (equipmentEdit.skill.level > 0)
				equipmentEdit.updateCurrent(equipmentEdit.skill.klass, equipmentEdit.skill.level - 1);
		});

		equipmentEdit.add(left);
		equipmentEdit.add(right);
		equipmentEdit.add(up);
		equipmentEdit.add(down);

		equipmentEdit.ok = createLabel();
		equipmentEdit.ok.position.y = -vh * 0.2;

		equipmentEdit.ok.addEventListener('click', () => {
			nowarg.skills[equipmentEdit.target.index] = {klass: equipmentEdit.skill.klass, level: equipmentEdit.skill.level};
			nowarg.skills.forEach(skill => skill.name = skill.klass.name);
			localStorage.setItem('skills-pre', JSON.stringify(nowarg.skills));
			equipmentEdit.close();
		});

		equipmentEdit.add(equipmentEdit.ok);

		const back = createLabel("Back");
		back.position.y = -vh * 0.25;
		back.addEventListener('click', () => equipmentEdit.close());
		equipmentEdit.add(back);

		equipmentEdit.close = function() {
			scene.interactive = true;
			points.each(point => point.interactive = true);
			this.children.each(child => child.interactive = false);
			scene.addEasing(
				new Easing(this)
					.add({opacity: 0}, 250, Easing.LINEAR)
					.trigger(this.parent.remove(this))
			);
		};
		equipmentEdit.updateCurrent = function(klass, level) {
			this.skill.klass = klass;
			this.skill.level = level;
			const changeing = this.skill.klass !== nowarg.skills[this.target.index].klass || this.skill.level !== nowarg.skills[this.target.index].level;
			this.ok.visible = changeing;
			this.ok.text = nowarg.skills[this.target.index].klass.prototype.className === 'Empty' ? 'Install' : 'Replace';
			this.ok.y = vh * 0.2;
			if (klass.prototype.className === 'Empty') {
				if (changeing) {
					this.name.text = ' ';
					this.ok.text = 'Uninstall';
					this.ok.y = vh * 0.06;
				} else this.name.text = 'No module';
			} else this.name.text = klass.skillName + ' ' + (level + 1);
			this.description.text = klass.getDescription(level);
		};
		console.log(equipmentEdit)
	}
	update(delta) {
		super.update(delta);
		this.time += delta * (this.position === 'shipmodify' ? 0.2 : 1);
		// Camera control
		this.player.quaternion.copy(new THREE.Quaternion());
		this.player.rotateX(Math.sin(this.time * 0.00075) * 0.25);
		this.player.rotateY(-Math.PI / 2 + this.time * 0.0004);

		this.labels.forEach(label => {
			label.material.opacity = 1 - Math.min(Math.max(Math.abs(this.camera.position.z - label.position.z - 50) - 10, 0) * 0.1, 1);
		});

		this.points.forEach(point => {
			const pos = this[point.data.parent].localToWorld(new THREE.Vector3(point.data.x, point.data.y, point.data.z))
								.project(this.camera);
			point.position.x = pos.x * vw * 0.5;
			point.position.y = pos.y * vh * 0.5;
		});

		this.frame++;
	}
}
