import * as THREE from "three";

import RenderPass from "../libs/three-effect/RenderPass";

import {SpriteText2D} from "three-text2d";

import {setCurrentScene, threeComposer} from "./main";
import {vw, vh, resized} from "./global";
import Easing from "./easing";

export const hitboxes = {
	CIRCLE: {}
};

export default class Scene extends THREE.EventDispatcher {
	constructor() {
		super();
		this.threeScene = new THREE.Scene();
		this.camera = new THREE.PerspectiveCamera(45, vw / vh, 1, 10000);
		this.threeScene.add(this.camera);
		this.UIScene = new THREE.Scene();
		this.UICamera = new THREE.OrthographicCamera(-vw / 2, vw / 2, vh / 2, -vh / 2, 1, 10000);
		this.UICamera.position.z = 5;
		this.UIScene.add(this.UICamera);
		this.threePasses = [
			new RenderPass(this.threeScene, this.camera, {renderToScreen: true}),
			new RenderPass(this.UIScene, this.UICamera, {renderToScreen: true, clear: false})
		];

		Easing.initIn(this);

		['pointstart', 'pointmove', 'pointend', 'click'].forEach(name => {
			this.addEventListener(name, e => {
				this.UIScene.traverse(children => {
					if (children.interactive === false) return;
					const pos = children.position.clone().project(this.UICamera);
					const rect = e.currentTarget.getBoundingClientRect();
			    const x = e.clientX - rect.left;
			    const y = e.clientY - rect.top;
					if (children instanceof SpriteText2D) {

					} else {
						if (children.hitbox = hitboxes.CIRCLE) {
							if (Math.hypot(x - vw / 2 - children.position.x, y - vh / 2 - children.position.y) <= children.radius)
								children.dispatchEvent(e);
						}
					}
				});
			});
		});
	}

	update(deltaTime) {
		this.updateEasings(deltaTime);
		this.UIScene.traverse(children => {
			if (children.material && children.material.opacity !== undefined) {
				let opacity = 1;
				for (let obj = children; obj.parent; obj = obj.parent) opacity *= obj.opacity === undefined ? 1 : obj.opacity;
				children.material.opacity = (children.selfOpacity === undefined ? 1 : children.selfOpacity) * opacity;
				if (opacity !== 1) children.material.transparent = true;
			}
		});
	}

	enterThisScene() {
		threeComposer.passes = this.threePasses;
		setCurrentScene(this);
	}

	prepareForRendering() {
		if (resized) {
			this.camera.aspect = vw / vh;
			this.camera.updateMatrixWorld();
			this.UICamera.left = -vw / 2;
			this.UICamera.right = vw / 2;
			this.UICamera.top = vh / 2;
			this.UICamera.bottom = -vh / 2;
			this.UICamera.updateMatrixWorld();
		}
		this.UIScene.traverse(children => {
			if (children.material) {
				children.material.transparent = children.material.opacity !== 1;
				children.material.visible = children.material.opacity !== 0;
			}
		});
	}

	static createAndEnter() {
		const scene = new this(...arguments);
		scene.enterThisScene();
		return scene;
	}
}
