import Sugar from "sugar";
Sugar.extend();

import * as THREE from "three";
import MeshLine from "three.meshline";

import EffectComposer from "../libs/three-effect/EffectComposer";

import {LoadingScene} from "./loading";
import TitleScene from "./title";
import {vw, vh, resize, reset} from "./global";

import {initPointerEvents} from "./input";

export const LoopRate = 60;

let threeRenderer;
export let threeComposer;

let currentScene;

export function setCurrentScene(scene) {
	currentScene = scene;
}

export function renderScreen() {
	currentScene.prepareForRendering();
	threeComposer.render();
}

window.onload = function() {
	const canvas = document.getElementById("screen");
	threeRenderer = new THREE.WebGLRenderer({canvas: canvas, antialias: true});
	threeRenderer.setSize(vw, vh);
	threeRenderer.setClearColor(new THREE.Color(0xffffff), 1.0);
	threeComposer = new EffectComposer(threeRenderer);

	initPointerEvents(canvas);

	/*window.addEventListener('resize', () => {
		resize(window.innerWidth, window.innerHeight);
	});*/

	['pointstart', 'pointmove', 'pointend'].forEach(name => {
		canvas.addEventListener(name, e => {
			currentScene.dispatchEvent(Object.merge(e.detail, {type: name, target: null, currentTarget: e.detail.target}, {descriptor: true}));
		});
	});

	['touchend', 'click'].forEach(name => {
		canvas.addEventListener(name, e => {
			currentScene.dispatchEvent(Object.merge(e, {type: 'click', target: null, currentTarget: e.target}, {descriptor: true}));
		});
	});

	LoadingScene.createAndEnter({
		THREE_Model_JSON: ["fighter", "bullet", "enem1", "airballoon"],
		THREE_Texture: ["plane", "explode"],
		GLSL: ["expvertex", "expfrag", "glowvertex", "glowfrag", "noisyglowvertex"]
	}, TitleScene);

	let previous = performance.now();
	(function loop() {
		window.setTimeout(loop, 1000 / LoopRate);
		const current = performance.now();
		currentScene.update && currentScene.update(current - previous);
		previous = current;
	})();

	(function drawLoop() {
		window.requestAnimationFrame(drawLoop);
		renderScreen();
		reset();
	})();
};

window.onunload = function() {
	threeRenderer.forceContextLoss();
}
